

.piano-roll {
  min-height: 0;
  min-width: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  margin: auto;
  user-select: none;
  border: 6px solid lightgray;
  border-radius: 3px;
  box-sizing: border-box;
}

.piano-roll.drum-sequencer {
  flex-shrink: 1;
}

.piano-roll-editor {
  min-height: 0px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
  flex-grow: 1;
}

.piano-roll-keys {
  min-height: 0px;
  width: 35px;
  display: flex;
  flex-flow: column-reverse nowrap;
  justify-content: flex-end;
}

.step-row {
  min-height: 0px;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.piano-key {
  height: 100%;
  min-height: 0px;
  padding: 0px;;
  margin: 0px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.piano-key p {
  font-size: 10px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  hyphens: auto;
}

.piano-key.black {
  background-color: var(--black);
  color: var(--light-grey);
}

.piano-key.white {
  background-color: var(--light-grey);
  color: var(--black);
}
